import style from './Main.module.css';
import Input from '@mui/material/Input';
import { useState, useRef } from 'react';
import Todo from '../Todo';

function Main() {
  const id = useRef(2); //可持有可變(mutable)的值，且具有current屬性，透過.current更改值不會re-render
  const [value, setValue] = useState('');
  const [completed, setCompleted] = useState(false);
  const [todos, setTodos] = useState([{ id: 1, content: '你好', completed: false }]);

  const handleInputChange = (e) => {
    setValue(e.target.value);
  };

  const handleAddTodos = (e) => {
    e.preventDefault();
    const newTasks = [...todos,{ id: id.current, content: value, completed: false }]
    setTodos(newTasks);
    console.log(todos);
    setValue('');
    id.current++;
  };

  const completeTask = (id, e) => {
    setCompleted(!completed);
    const newTasks = [...todos];
/*     const filterOutWhichOneIsDone = newTasks.filter(item => {
      if(item.id === id){
        item.completed = !completed;
        return item;
      }
    } );
    console.log(newTasks, filterOutWhichOneIsDone); */
    //setTodos(...todos, filterOutWhichOneIsDone);
     newTasks.forEach(item => {
      if(item.id === id){
        item.completed = !completed
      }
    }) 
    //setTodos(newTasks);
    //console.log(todos);
  }

  return (
    <main className={style.main}>
      <form onSubmit={handleAddTodos}>
        <label className={style.todolabel} htmlFor="todo-input">
          Add to the todo list
        </label>
        <div className={style.inputreg}>
        <Input placeholder="Here Add todo" value={value} onChange={handleInputChange} className={style.todoinput} type="text" id="todo-input" />
        <button type="submit" className={style.addbtn}>
          ADD ITEM
        </button>
        </div>
      </form>
      <div>
          { todos.map(todo => <Todo completeTask={completeTask} completed={completed}  key={todo.id} todo={todo}/>) }
      </div>
    </main>
  );
}

export default Main;
