import React from "react";
import style from './Todo.module.css';
import Checkbox from '@mui/material/Checkbox';
import Delete from '@mui/icons-material/Delete';

 //使用React.Memo()當props沒有改變就不re-render
const Todo =({completeTask, completed, todo}) => {//使用解構把props這個物件內的值取出來(Props come from the Parent Component )
  console.log('Render Todo');
  //console.log(todo);

  return (
    <ul className={style.todogroup} data-todo-id={todo.id}>
      <li className={style.todoli}>
        <span style={{textDecoration: todo.completed ? 'line-through' : ''}} className={style.todocontent}>{todo.content}</span>
        <div className={style.action}>
          <Checkbox checked={todo.completed} onChange={(e) => completeTask(todo.id, e) }  className={style.todoicon}>isCheck</Checkbox>
          <Delete className={style.todoicon} >delete</Delete>
        </div>
      </li>
    </ul>
  );
};


export default Todo;